import { Injectable } from '@angular/core';
import {User} from './user.model';
import {Card} from './card.model';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {environment} from '../../environments/environment';
import {map} from 'rxjs/operators';
import {Subject} from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class UserService {
  private users: User[] = [];
  private usersUpdated = new Subject<User[]>();
  selectedUser: User = {
    category: '',
    fullName: '',
    password: '',
    email: ''
  };
  constructor(private http: HttpClient) { }

  postUser(user: User) {
    return this.http.post(environment.apiBaseUrl + '/sign/reg', user);
  }
  postCard() {
    return this.http.post(environment.apiBaseUrl + '/products', null);
  }
  getUsers() {
   this.http.get<{message: string, users: any}>(environment.apiBaseUrl + '/products')
   .pipe(map((postData) => {
     return postData.users.map(post => {
       return {
          fullName: post.fullName,
          email: post.email,
          category: post.category
         // password: post.password
       };
     });
   })).subscribe(transformedUsers => {
        this.users = transformedUsers;
        this.usersUpdated.next([...this.users]);
   });
}

getUsers2() {
  this.http.get<{message: string, users: any}>(environment.apiBaseUrl + '/gaming/laptops')
  .pipe(map((postData) => {
    return postData.users.map(post => {
      return {
         fullName: post.fullName,
         email: post.email,
         category: post.category
        // password: post.password
      };
    });
  })).subscribe(transformedUsers => {
       this.users = transformedUsers;
       this.usersUpdated.next([...this.users]);
  });
}

  getUserUpdateListener() {
   return this.usersUpdated.asObservable();
 }
}
