import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SignInComponent } from './components/sign-in/sign-in.component';
import { PageNotFoundComponent } from './components/page-not-found/page-not-found.component';
import {HomeComponent} from './components/home/home.component';
import { RegisterComponent } from './components/register/register.component';
import { ProductsComponent } from './components/products/products.component';
import { AboutUsComponent } from './components/about-us/about-us.component';
import { CardComponent } from './components/card/card.component';
import { GamingLaptopsComponent } from './components/gaming-laptops/gaming-laptops.component';

const routes: Routes = [
  { path: '', redirectTo: 'home', pathMatch: 'full'},
  { path: 'home', component: HomeComponent},
  { path: 'sign', component: SignInComponent},
  {path: 'sign/reg', component: RegisterComponent},
  {path: 'products', component: ProductsComponent},
  {path: 'about', component: AboutUsComponent},
  {path: 'card', component: CardComponent},
  {path: 'products/gaming/laptops', component: GamingLaptopsComponent},
  {path: 'sign/reg/sign', redirectTo: 'sign', pathMatch: 'full'},
  { path: '**', component: PageNotFoundComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }

//       - (  -  )
export const routingComponents = [HomeComponent, SignInComponent, PageNotFoundComponent, RegisterComponent];
