import { Component, OnInit } from '@angular/core';
import {RegisterModel} from '../../models/register.model';
import {FormGroup, FormBuilder, Validators, NgForm} from '@angular/forms';
import {Subscription} from 'rxjs';
import {UserService} from '../../models/user.service';
@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {
  showSuccessMessage: boolean;
  serverErrorMessage: string;
  // users: User[] = [];
  // private usersSub: Subscription;
  user: RegisterModel = new RegisterModel();
  registerForm: FormGroup;
  hidePass = true;
  RgUser = this.user.fullName;
  users = [{name: this.RgUser}];
  constructor(private formBuilder: FormBuilder, public userService: UserService) { }

  ngOnInit() {
     this.registerForm = this.generateForm();
  }
  private generateForm(): FormGroup {
    // tslint:disable-next-line: prefer-const
    let nameFormControl = this.formBuilder.control(null, [
        Validators.required,
        Validators.minLength(4),
        Validators.maxLength(20)
    ]);
    // tslint:disable-next-line: prefer-const
    let passwordFormControl = this.formBuilder.control(null, [
      Validators.required,
      Validators.minLength(4),
      Validators.maxLength(15)
    ]);
    // tslint:disable-next-line: prefer-const
    let emailFormControl = this.formBuilder.control(null, [
      Validators.required,
      Validators.email
    ]);
    return this.formBuilder.group({
      fullName: nameFormControl,
      email: emailFormControl,
      password: passwordFormControl
    });
  }

  onRegisterSubmit(form: NgForm) {
    this.userService.postUser(form.value).subscribe(
      res => {this.showSuccessMessage = true; setTimeout(() => this.showSuccessMessage = false, 4000); this.resetForm(form); },
      err => {if (err.status === 422) {
              this.serverErrorMessage = err.error.join('<br/>');
      } else {
        this.serverErrorMessage = 'Something went wrong, contact the admin!';
      }
    }
    );
  }
  resetForm(form: NgForm) {
    this.userService.selectedUser = {
      category: '',
      fullName: '',
      password: '',
      email: '',
    };
    form.resetForm();
  }
}
